import React, { useState } from 'react'
import './App.css';

function App() {
  const [time,setTime]=React.useState(0);
  const[timeOn,setTimeon]=React.useState(false);

  React.useEffect(()=>{
    let interval=null;
    if(timeOn){
      interval=setInterval(()=>{
       setTime(prevTime=>prevTime+10)
      },10)

    }else{
     clearInterval(interval)
    }
    return ()=>clearInterval(interval)
  },[timeOn])

  return (
    <div className="container my-3">
      <div class="card text-center">
  <div class="card-header">
    Stop Watch
  </div>
  <div class="card-body">
  
  <button type="button" class="btn btn-danger"><span>{('0'+Math.floor((time/60000)%60)).slice(-2)}</span></button>
 :<button type="button" class="btn btn-danger"><span>{('0'+Math.floor((time/1000)%60)).slice(-2)}</span></button>:
 <button type="button" class="btn btn-danger"><span>{('0'+((time/10)%100)).slice(-2)}</span></button>
  </div>
  <div class="card-footer text-muted">
        {!timeOn && time==0 &&(
         <button onClick={()=> setTimeon(true)}>Start</button>
          
        )}
        {timeOn &&(
          <button onClick={()=> setTimeon(false)}>Stop</button>
        )}
        {!timeOn && time !=0 &&(
         <button onClick={()=> setTimeon(true)}>Resume</button>
          
        )}
        {!timeOn && time>0 &&(
         <button onClick={()=> setTime(0)}>Reset</button>
          
        )}
        
  </div>
</div>
      
</div>     
  );
}

export default App;
